* {
  font-family: Roboto;
}

section {
  width: 100%;
  display: inline-block;
  background: #ccc;
  height: 60vh;
  text-align: center;
  font-size: 22px;
  font-weight: 700;
  text-decoration: underline;
}

.footer-distributed {
  margin-top: 80px;
  /* display: flex; */
  justify-content: center;
  background-color: #070a1b;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font: normal 16px sans-serif;
  padding: 45px 50px;
}

.footer-distributed .footer-left p {
  color: #8f9296;
  font-size: 14px;
  margin: 0;
}
/* Footer links */

.footer-distributed p.footer-links {
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
  margin: 0 0 10px;
  padding: 0;
  transition: ease 0.25s;
}

.footer-distributed p.footer-links a {
  display: inline-block;
  line-height: 1.8;
  text-decoration: none;
  /* color: inherit; */
  transition: ease 0.25s;
  margin-right: 24px;
}

.footer-distributed .footer-links a:before {
  content: "·";
  font-size: 20px;
  left: 0;
  color: #fff;
  display: inline-block;
  padding-right: 5px;
}

.footer-distributed .footer-links .link-1:before {
  content: none;
}

.footer-distributed .footer-right {
  float: right;
  margin-top: 6px;
  max-width: 180px;
}

.footer-distributed .footer-right a {
  display: inline-block;
  width: 35px;
  height: 35px;
  /* background-color: #33383b; */
  border-radius: 2px;
  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;
  margin-left: 3px;
  transition: all 0.25s;
}

.footer-distributed .footer-right a:hover {
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
}

.footer-distributed p.footer-links a:hover {
  text-decoration: underline;
}

/* Media Queries */

@media (max-width: 600px) {
  .footer-distributed .footer-left,
  .footer-distributed .footer-right {
    text-align: center;
  }
  .footer-distributed .footer-right {
    float: none;
    margin: 0 auto 20px;
  }
  .footer-distributed .footer-left p.footer-links {
    line-height: 1.8;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: rgb(22, 46, 174);
  background: #251e31;
  background-image: url(/static/media/cine.7f6903b2.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-thumb {
  background-color: rgb(181, 181, 181);
  border-radius: 10px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a {
  color: white;
}
p {
  color: white;
  margin: 0;
}
h1 {
  color: white;
  margin: 0;
  padding: 0;
}
/* ---------------MOVIES AND CONTAINER MOVIES */

.container-movies {
  display: flex;
  justify-content: center;
  overflow-x: scroll;
  flex-wrap: wrap;
  width: 90%;
  margin: 0 auto;
  padding: 30px;
}

.container-movies a {
  border-radius: 2px;
  border-radius: 8px;
  max-width: 300px;
  margin: 15px 15px 0 0;
}

.container-movies div a {
  margin: 15px 15px 0 0;
}

.movie-title {
  font-size: 18px;
  padding: 10px 0 10px 0;
}

.moviesContainer {
  display: flex;
  flex-direction: column;
}

.carrouselMovies {
  display: flex;
  overflow-x: scroll;
  width: 90%;
  margin: 0 auto;
  padding: 20px;
  /* max-height: 450px; */
  /* border: 2px solid blue; */
}

.cardMovie {
  position: relative;
  border-radius: 10px;
  margin-right: 20px;
  background-color: #051049;
}

.cardMovie img {
  height: 300px;
  width: 200px;
  border-radius: 2px;
}

.title-container {
  height: 70px;
}

.cardMovie:hover .hoverinfo {
  visibility: visible;
  transition: 0.5 all;
}

.hoverinfo {
  position: absolute;
  bottom: 0;
  height: 50%;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0) 20%,
    rgba(5, 16, 73, 1) 80%
  );
  /* width: 100%; */
  border-radius: 10px;
  width: 180px;
  visibility: hidden;
  transition: 0.5 all;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: end;
}

.starIcon {
  color: yellow;
  margin-right: 10px;
}

